import React from 'react'
import {BrowserRouter, withRouter, Route , Link} from 'react-router-dom'
import '../App.css'
import {Checkbox, Button} from 'antd'
import GoFunding from './GoFunding'
class InitiateFunding extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            disabled: true
        }
    }

    onChange = (e) => {
        if(e.target.checked) {
            this.setState({
                disabled: false                 
            })
        }else{
            this.setState({     
                disabled: true
            })
        }
    }

    render() {
        return (
            <div className="InitFundContentRouter">
                <div className="InitFundContent">
                    <div className="InitFundTop">
                        <span style={{fontFamily: "Mircosoft YaHei", fontSize: "16px", fontWeight: "400"}}></span>
                    </div>
                    <div style={{width: "100%", height: "100%", display:"flex"}}>
                        <div style={{flexGrow: "1"}}>
                            <img src='images/gofund.png' />
                        </div>
                        
                        <div style={{flexGrow: "20"}}>
                            <div style={{width: "80%", height: "70%", margin: "70px"}}>
                                <p style={{color: "#AB4A53", fontSize: "20px",fontWeight: "500"}}>......</p>
                                <p>
                                     
                                </p>
                                <Checkbox style={{marginTop: "10px"}} onChange={this.onChange}><a href=""></a><a href=""></a></Checkbox>
                                <br/>
                                <Button 
                                    style={{width: "150px", fontSize: "16px", height: "50px", marginTop: "30px", color:"#fff", backgroundColor: "#FE645C"}}
                                    disabled={this.state.disabled}
                                    >
                                    <Link to='/GoFunding'></Link>
                                </Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(InitiateFunding)