import React from 'react'
import { Modal, message, Cascader, InputNumber, Form, Button, Col, Row, Input, Select, DatePicker, Switch } from 'antd'
import { web3, AppContract, getContract, saveJsonToIPFS, readJsonFromIpfs, getPayGas } from '../Utils'
import residences from '../provinceData'
import moment from 'moment'
import '../App.css';

const { Option } = Select;
const confirm = Modal.confirm;

class EditUserInfo extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      account: '',
      isCheck: false,
      editVisible: false,
      mateMaskIsLogin: false,
      user: undefined,
      userService: undefined,
      disabled: true,
      nickname: '',
      age: '',
      gender: '',
      regDate: '',
      email: '',
      brithday: '',
      residence: [],
      detailedAddr: '',
      phoneNumber: '',
      realName: '',
      zipcode: '',
      description: ''
    }
    this.init()
  }


  init = async () => {
    const [account] = await web3.eth.getAccounts();
    console.log(":" + account)
    if (account === undefined) {
      Modal.error({
        title: '',
        content: 'MateMask'
      })
      return;
    }
    const userServiceAddr = await AppContract.methods.userService().call()
    const userService = await getContract("UserService", userServiceAddr)

    const a = await userService.methods.getAllUser().call({
      from: account
    })
    console.log(a)
    // 
    const isCheck = await userService.methods.checkUser(account).call({
      from: account
    })

    // true
    // 
    if (!isCheck) {
      Modal.info({
        okText: '',
        title: '',
        content: (
          <div>
            <p></p>
          </div>
        ),
        // onOk() {},
      });
      this.setState({
        disabled: false
      })
    } else {
      const hide = message.loading('', 0)
      // User
      // ipfsipfs
      // msg.senderfrom
      const UserAddr = await userService.methods.getUserByCurrent().call({
        from: account
      })
      const user = await getContract("User", UserAddr)
      const userInfoArr = await user.methods.getUserInfo().call()

      // undefined
      if (userInfoArr !== undefined) {
        if ((userInfoArr[0] !== '' && userInfoArr[0] !== undefined)
          && (userInfoArr[1] !== '' && userInfoArr[1] !== undefined)) {
          const userInfo = await readJsonFromIpfs(userInfoArr[0], userInfoArr[1])
          
          this.setState({ user: user })
          console.log(userInfo)
          // 
          this.setState({
            nickname: userInfo.nickname,
            age: userInfo.age,
            gender: userInfo.gender,
            email: userInfo.email,
            brithday: userInfo.brithday,
            residence: userInfo.residence,
            detailedAddr: userInfo.detailedAddr,
            phoneNumber: userInfo.phoneNumber,
            zipcode: userInfo.zipcode,
            realName: userInfo.realName,
            description: userInfo.description,
            regDate: userInfo.regDate
          })

          this.props.form.setFieldsValue({
            "nickname": this.state.nickname,
            "gender": this.state.gender,
            "age": this.state.age,
            "email": this.state.email,
            "brithday": moment(this.state.brithday),
            "residence": this.state.residence,
            "detailedAddr": this.state.detailedAddr,
            "phoneNumber": this.state.phoneNumber,
            "realName": this.state.realName,
            "zipcode": this.state.zipcode,
            "description": this.state.description
          });
        }
        hide()
      }
    }
    this.setState({
      account: account,
      isCheck: isCheck,
      userService: userService,
      // user: user
    })
  }

  // IPFS
  // hash
  onSubmit = async () => {
    const nickname = this.props.form.getFieldValue('nickname')
    if (nickname === undefined) {
      message.error("")
      return
    }
    const age = this.props.form.getFieldValue('age')
    if (age === undefined) {
      message.error("")
      return
    }
    const gender = this.props.form.getFieldValue('gender')
    if (gender === undefined) {
      message.error("")
      return
    }

    const email = this.props.form.getFieldValue('email')
    if (email === undefined) {
      message.error("")
      return
    }

    // let brithday = this.props.form.getFieldValue('brithday')
    // console.log(brithday)
    var brithday
    if (this.props.form.getFieldValue('brithday') !== undefined) {
      brithday = this.props.form.getFieldValue('brithday').format('YYYY-MM-DD')
    } else {
      message.error("")
      return
    }

    const residence = this.props.form.getFieldValue('residence')
    const detailedAddr = this.props.form.getFieldValue('detailedAddr')
    var ShippingAddress
    if (residence === undefined) {
      message.error("")
    } else {
      if (detailedAddr !== '') {
        ShippingAddress = residence[0] + residence[1] + residence[2] + detailedAddr
      } else {
        message.error("")
      }
    }

    const phoneNumber = this.props.form.getFieldValue('phoneNumber')
    if (phoneNumber === undefined) {
      message.error("")
      return
    }
    const zipcode = this.props.form.getFieldValue('zipcode')
    if (zipcode === undefined) {
      message.error("")
      return
    }

    const realName = this.props.form.getFieldValue('realName')
    if (realName === undefined) {
      message.error("")
      return
    }

    let description = this.props.form.getFieldValue('description')
    if (description === undefined) {
      description = ''
    }

    // console.log(this.state.account)
    // console.log(nickname)
    // console.log(email)
    // console.log(age)
    // console.log(gender)
    // console.log(brithday)
    // console.log(ShippingAddress)
    // console.log(phoneNumber)
    // console.log(zipcode)
    // console.log(description)

    let userInfo = new Object();
    userInfo.hash = this.state.account
    userInfo.nickname = nickname
    userInfo.email = email
    userInfo.age = age
    userInfo.regDate = this.state.regDate
    userInfo.gender = gender
    userInfo.brithday = brithday
    userInfo.residence = residence
    userInfo.detailedAddr = detailedAddr
    userInfo.ShippingAddress = ShippingAddress
    userInfo.phoneNumber = phoneNumber
    userInfo.zipcode = zipcode
    userInfo.realName = realName
    userInfo.description = description
    userInfo.JoinOrders = []
    userInfo.InitiatedOrders = []

    console.log(userInfo)
    var conContent = ''
    // 
    if (this.state.isCheck) {
      conContent = ""
    } else {
      conContent = ""
    }
    confirm({
      title: '',
      content: conContent,
      okText: '',
      cancelText: '',
      onOk: async () => {
        // 
        console.log(userInfo)
        console.log(JSON.stringify(userInfo))
        const hide = message.loading('');
        const userIpfsHash = await saveJsonToIPFS(userInfo)

        console.log("hash" + userIpfsHash)
        const hash1 = userIpfsHash.slice(0, 23)
        const hash2 = userIpfsHash.slice(23)
        const hash1Val = web3.utils.asciiToHex(hash1)
        const hash2Val = web3.utils.asciiToHex(hash2)
        console.log(this.state.isCheck)
        // 
        if (this.state.isCheck) {
          // useruserService
          await this.state.user.methods.updataUser(hash1Val, hash2Val).send({
            from: this.state.account,
            gas: '5000000'
          })
        } else {
          await this.state.userService.methods.createUser(hash1Val, hash2Val).send({
            from: this.state.account,
            gas: '5000000'
          })
        }
        hide()
        message.success("!", 4)
        this.props.history.push("/EditUserInfo")
      },
      onCancel() {
        console.log('Cancel');
      },
    });
  }

  inputChange = (e) => {
    console.log(e.target.name + ":" + e.target.value)
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  ageChange = (e) => {
    this.setState({
      age: e
    })
  }

  genderChange = (e) => {
    this.setState({
      gender: e
    })
  }

  brithdayChange = (e) => {
    this.setState({
      brithday: e
    })
  }

  residenceChange = (e) => {
    this.setState({
      residence: e
    })
  }

  // switch
  onSwitchChange = (isSwitch) => {
    console.log(isSwitch)
    this.setState({
      disabled: isSwitch
    })
  }

  hideModal = () => {
    this.setState({
      editVisible: false
    })
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const prefixSelector = getFieldDecorator('prefix', {
      initialValue: '86',
    })(
      <Select style={{ width: 70 }}>
        <Option value="86">+86</Option>
        <Option value="87">+87</Option>
      </Select>
    );

    return (<div>
      <Modal
        title=""
        visible={this.state.editVisible}
        onOk={this.hideModal}
        onCancel={this.hideModal}
        okText=""
        cancelText=""
      >
        <p>
          
              </p>
      </Modal>
      <div
        style={{
          width: '100%',
          borderBottom: '1px solid #e9e9e9',
          padding: '15px 16px',
          background: '#2FAC6A',
          textAlign: 'right',
          borderTopLeftRadius: "8px",
          borderTopRightRadius: "8px"
        }}
      >
        {/* <h3></h3> */}
        <Switch defaultChecked checkedChildren="" unCheckedChildren="" onChange={this.onSwitchChange} style={{ backgroundColor: "#555" }} />
      </div>
      <Form layout="vertical" hideRequiredMark style={{ padding: "20px", backgroundColor: "#F0FFF7" }}>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item label="">
              {this.state.account}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="* ">
              {getFieldDecorator('nickname', {
                rules: [{ required: true, message: '' }],
              })(
                <Input
                  style={{ width: '100%' }}
                  // placeholder=""
                  name="nickname"
                  onChange={this.inputChange}
                  disabled={this.state.disabled}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item label="* ">
              {getFieldDecorator('age', {
                rules: [{ required: true, message: '' }],
              })(
                <InputNumber
                  style={{ width: '100%' }}
                  placeholder=""
                  name="age"
                  min={1}
                  initialValue={1}
                  onChange={this.ageChange}
                  disabled={this.state.disabled}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="* ">
              {getFieldDecorator('gender', {
                rules: [{ required: true, message: '' }],
              })(
                <Select placeholder=""
                  onChange={this.genderChange}
                  disabled={this.state.disabled}
                  name="gender"
                >
                  <Option value="male"></Option>
                  <Option value="famale"></Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item label="* ">
              {getFieldDecorator('email', {
                rules: [{ required: true, message: '' },
                { type: 'email', message: '' }
                ],
              })(
                <Input
                  style={{ width: '100%' }}
                  disabled={this.state.disabled}
                  placeholder=""
                  name="email"
                  onChange={this.inputChange}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="* ">
              {getFieldDecorator('brithday', {
                rules: [{ required: true, message: '' }],
              })(
                <DatePicker
                  placeholder=""
                  name="brithday"
                  disabled={this.state.disabled}
                  format="YYYY-MM-DD"
                  style={{ width: '100%' }}
                  onChange={this.brithdayChange}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item
              label="* "
            >
              {getFieldDecorator('residence', {
                initialValue: ['', '', ''],
                rules: [{ type: 'array', required: true, message: '!' }],
              })(
                <Cascader
                  options={residences}
                  name="residence"
                  disabled={this.state.disabled}
                  onChange={this.residenceChange}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item
              label="* "
            >
              {getFieldDecorator('detailedAddr', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input
                  style={{ width: '100%' }}
                  name="detailedAddr"
                  disabled={this.state.disabled}
                  placeholder=""
                  onChange={this.inputChange}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item label="* ">
              {getFieldDecorator('phoneNumber', {
                rules: [{ required: true, message: '' },
                { pattern: new RegExp(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/, "g"), message: '' }
                ],
              })(
                <Input
                  style={{ width: '100%' }}
                  name="phoneNumber"
                  placeholder=""
                  type='number'
                  disabled={this.state.disabled}
                  addonBefore={prefixSelector}
                  onChange={this.inputChange}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="* ">
              {getFieldDecorator('zipcode', {
                rules: [{ required: true, message: '' },
                { pattern: new RegExp(/^[1-9][0-9]{5}$/, "g"), message: '' }
                ],
              })(
                <Input
                  style={{ width: '100%' }}
                  name="zipcode"
                  disabled={this.state.disabled}
                  placeholder=""
                  type='number'
                  onChange={this.inputChange}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item label="* ">
              {getFieldDecorator('realName', {
                rules: [{ required: true, message: '' }],
              })(
                <Input
                  disabled={this.state.disabled}
                  style={{ width: '100%' }}
                  name="realName"
                  placeholder=""
                  type='text'
                  onChange={this.inputChange}
                />
              )}
            </Form.Item>
          </Col>
          <Col></Col>
        </Row>
        <Row gutter={16}>
          <Col span={24}>
            <Form.Item label="">
              {getFieldDecorator('description')(
                <Input.TextArea
                  rows={3}
                  disabled={this.state.disabled}
                  name='description'
                  placeholder=""
                  onChange={this.inputChange}
                />)
              }
            </Form.Item>
          </Col>
        </Row>
      </Form>
      <div
        style={{
          width: '100%',
          borderBottom: '1px solid #e9e9e9',
          padding: '15px 16px',
          textAlign: 'right',

        }}
      >
        <Button onClick={this.onSubmit} type="primary" disabled={this.state.disabled}>
          
                </Button>
      </div>

    </div>)
  }
}

export default Form.create()(EditUserInfo)