                                <div style={{ width: "99%" }} >
                                    <Player ref="player" videoId="video-1" autoPlay>
                                        <source src={ipfsPrefix + this.state.GoodsInfo.goodsDecVideo} />
                                    </Player>
                                </div>
                                {
                                    this.state.GoodsInfo.goodsDecImgs.map((v, i) => {
                                        return <img key={i} src={ipfsPrefix + v} />
                                    })
                                }
