import React, { Component } from 'react'
import {Layout,Modal, message, Cascader, BackTop, InputNumber,Affix,  Drawer, Form, Button, Col, Row, Input, Select, DatePicker} from 'antd'
import {BrowserRouter, Route} from 'react-router-dom'
import {web3, AppContract, getContract, saveJsonToIPFS, getPayGas} from './Utils'
import Header from './pages/HeaderComponent'
import MenuComponent from './pages/MenuComponent'
import Main from './pages/Main'
import InitiateFunding from './pages/InitiateFunding'
import residences from './provinceData'
import Shop from './pages/Shop'
import GoFunding from './pages/GoFunding'
import moment from 'moment'
import PersonalCenter from './pages/PersonalCenter'
import FundingDetail from './pages/FundingDetail'
import FundingNewOnline from './pages/FundingNewOnline'
import FundingComingEnd from './pages/FundingComingEnd'
import './App.css';

const { Option } = Select;
const {Footer, Content} = Layout
const confirm = Modal.confirm;

class App extends Component {
  
  constructor(props) {
    super(props)
    this.state={
      account: '',
      isCheck: false,
      visible: false,
      drawerVisible: false,
      mateMaskIsLogin: false,
      userService: undefined,
      nickname: '',
      age:'',
      gender:'', 
      email:'',
      brithday:'',
      residence:'', 
      detailedAddr:'',
      phoneNumber:'',
      zipcode: '',
      description:'',
      userServiceAddr: '',
    }
    this.init()
  }

  init = async() => {
    const [account] = await web3.eth.getAccounts();
    console.log(":" + account)
    if(account === undefined) {
      Modal.error({title: '',
                  content: 'MateMask'})
      return;
    }
    const userServiceAddr = await AppContract.methods.userService().call({
      from: account
    })
    // 
    const userService = await getContract("UserService", userServiceAddr)
    this.setState({userService,userServiceAddr})
    console.log(userService)
    const isCheck = await userService.methods.checkUser(account).call({
      from: account
    })
    console.log(isCheck)
    if(isCheck){
      this.setState({
        visible: false
      })
    } else {
      this.setState({
        visible: true
      })
    }
    this.setState({
      isCheck:isCheck,
      account: account
    })
  }

  hideModal = () => {
    // Modal.error({title: '',
    //             content: 'MateMask'})
    message.warning("", 4)
    this.setState({
      visible: false,
    });
  }

  showDrawer = () => {
      this.setState({
        visible: false,
        drawerVisible: true
      });
  };
  // IPFS
  // hash
  onSubmit = () => {
    const account = this.state.account
    const nickname = this.props.form.getFieldValue('nickname')
    if(nickname === undefined) {
      message.error("")
      return
    }
    const age = this.props.form.getFieldValue('age')
    if(age === undefined) {
      message.error("")
      return
    } 
    const gender = this.props.form.getFieldValue('gender')
    if(gender === undefined) {
      message.error("")
      return
    }

    const email = this.props.form.getFieldValue('email')
    if(email === undefined) {
      message.error("")
      return
    }

    // let brithday = this.props.form.getFieldValue('brithday')
    // console.log(brithday)
    var brithday
    if(this.props.form.getFieldValue('brithday') !== undefined) {
      brithday = this.props.form.getFieldValue('brithday').format('YYYY-MM-DD')
    }else{
      message.error("")
      return
    }

    const residence = this.props.form.getFieldValue('residence')
    const detailedAddr = this.props.form.getFieldValue('detailedAddr')
    var ShippingAddress
    if(residence === undefined) {
      message.error("")
    } else {
      if(detailedAddr !== ''){
        ShippingAddress = residence[0] + residence[1] + residence[2] + detailedAddr
      }else{
        message.error("")
      }
    }
    
    const phoneNumber = this.props.form.getFieldValue('phoneNumber')
    if(phoneNumber === undefined ) {
      message.error("")
      return
    }
    const zipcode = this.props.form.getFieldValue('zipcode')
    if(zipcode === undefined) {
      message.error("")
      return
    }

    const realName = this.props.form.getFieldValue('realName')
    if(realName === undefined) {
      message.error("")
      return
    }
    
    let description = this.props.form.getFieldValue('description')
    if(description === undefined) {
      description = ''
    }

    console.log(this.state.account)
    console.log(nickname)
    console.log(age)
    console.log(gender)
    console.log(email)
    console.log(brithday)
    console.log(ShippingAddress)
    console.log(phoneNumber)
    console.log(zipcode)
    console.log(description)
    console.log(realName)

    let userInfo = new Object();
    userInfo.hash = this.state.account
    userInfo.nickname = nickname
    userInfo.age = age
    userInfo.regDate = moment().format('YYYY-MM-DD')
    userInfo.gender = gender
    userInfo.email = email
    userInfo.realName = realName
    userInfo.brithday = brithday
    userInfo.detailedAddr = detailedAddr
    userInfo.residence = residence
    userInfo.ShippingAddress = ShippingAddress
    userInfo.phoneNumber = phoneNumber
    userInfo.zipcode = zipcode
    userInfo.description = description
    userInfo.JoinOrders = []
    userInfo.InitiatedOrders = []

    console.log(userInfo)

    confirm({
      title: '',
      content: '',
      okText: '',
      cancelText: '',
      onOk: async()=>{
        // 
        // message.warning("", 4)
        console.log(userInfo)
        console.log(JSON.stringify(userInfo))
        // const hide = message.loading('');
        const userIpfsHash = await saveJsonToIPFS(userInfo)
        // hide()
        console.log(userIpfsHash)
        const hash1 = userIpfsHash.slice(0,23)
        const hash2 = userIpfsHash.slice(23)
        const hash1Val = web3.utils.asciiToHex(hash1)
        const hash2Val = web3.utils.asciiToHex(hash2)


        await this.state.userService.methods.createUser(hash1Val, hash2Val).send({
          from: this.state.account,
          gas: '5000000'
        })
        message.success("!", 4)
        this.forceUpdate();
        this.setState({
          drawerVisible: false,
        });
      },
      onCancel() {
        console.log('Cancel');
      },
    });

   

    // console.log(this.props.form.getFieldValue('brithday').format('YYYY-MM-DD'))

    // 
    // console.log(this.state.account)
    // console.log(this.state.nickname)
    // console.log(this.state.age)
    // console.log(this.state.gender)
    // console.log(this.state.brithday)
    // console.log(this.state.residence[0] + this.state.residence[1] + this.state.residence[2])
    // console.log(this.state.detailedAddr)
    // console.log(this.state.phoneNumber)
    // console.log(this.state.zipcode)
    // console.log(this.state.description)
  }

  inputChange = (e) => {
    console.log(e.target.name + ":" + e.target.value)
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  ageChange = (e) => {
    this.setState({
      age: e
    })
  }
  
  genderChange = (e) => {
    this.setState({
      gender: e
    })
  }

  brithdayChange = (e) => {
    this.setState({
      brithday: e
    })
  }
  
  residenceChange = (e) => {
    this.setState({
      residence: e
    })
  }

  onClose = () => {
    // 
    console.log(this.state.nickname)
    var flag = false
    if(this.state.nickname !== '' || this.state.email !== '' || this.state.phoneNumber !== '') {
      confirm({
        title: '',
        content: '',
        okText: '',
        okType: 'danger',
        cancelText: '',
        onOk: ()=>{
          // 
          message.warning("", 4)
          
          this.setState({
            drawerVisible: false,
          });
        },
        onCancel() {
          console.log('Cancel');
        },
      });
    }else{
      message.warning("", 4)
      this.setState({
        drawerVisible: false,
      });
    }
    
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const prefixSelector = getFieldDecorator('prefix', {
      initialValue: '86',
    })(
      <Select style={{ width: 70 }}>
        <Option value="86">+86</Option>
        <Option value="87">+87</Option>
      </Select>
    );
    // 
    return (
      <BrowserRouter className='App'>
        <BackTop />
        <Modal
          title=""
          visible={this.state.visible}
          onOk={this.showDrawer}
          onCancel={this.hideModal}
          okText=""
          cancelText=""
        >
          <p></p>
        </Modal>
        <Layout className='layout'>
          <Header></Header>
          <Affix offsetTop={this.state.top}>
            <MenuComponent></MenuComponent>
          </Affix>
          
          <Content style={{ padding: '0 50px' }}>
            <Route path='/' exact component={Main}></Route>
            <Route path='/PersonalCenter' component={PersonalCenter}></Route>
            <Route path='/InitiateFunding' component={InitiateFunding}></Route>
            <Route path='/Shop' component={Shop}></Route>
            <Route path='/GoFunding' component={GoFunding}></Route> 
            <Route path='/FundingNewOnline' component={FundingNewOnline}></Route> 
            <Route path='/FundingComingEnd' component={FundingComingEnd}></Route> 
            <Route path='/FundingDetail' component={FundingDetail}></Route>
          </Content>
          <Footer style={{ textAlign: 'center' }}>
            Howie wong  2019 Created by Howie wong
          </Footer>
        </Layout>
        <div>
            <Drawer
            title="*"
            width={720}
            onClose={this.onClose}
            visible={this.state.drawerVisible}
            >
            <Form layout="vertical" hideRequiredMark >
                <Row gutter={16}>
                  <Col span={12}>
                      <Form.Item label="">
                        {this.state.account}
                      </Form.Item>
                  </Col>
                  <Col span={12}>
                      <Form.Item label="* ">
                      {getFieldDecorator('nickname', {
                          rules: [{ required: true, message: '' }],
                      })(
                          <Input
                          style={{ width: '100%' }}
                          placeholder=""
                          name="nickname"
                          onChange={this.inputChange}
                          />
                      )}
                      </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col span={12}>
                      <Form.Item label="* ">
                      {getFieldDecorator('age', {
                          rules: [{ required: true, message: '' }],
                      })(
                        <InputNumber
                        style={{ width: '100%' }}
                        placeholder=""
                        name="age"
                        min={1}
                        initialValue={1}
                        onChange={this.ageChange}
                        />
                      )}
                      </Form.Item>
                  </Col>
                  <Col span={12}>
                      <Form.Item label="* ">
                      {getFieldDecorator('gender', {
                          rules: [{ required: true, message: '' }],
                      })( 
                          <Select placeholder="" 
                                  onChange={this.genderChange}
                          >
                          <Option value="male"></Option>
                          <Option value="famale"></Option>
                          </Select>
                      )}
                      </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col span={12}>
                      <Form.Item label="* "> 
                      {getFieldDecorator('email', {
                          rules: [{ required: true, message: '' },
                                  { type: 'email', message: ''}
                        ],
                      })(
                          <Input
                          style={{ width: '100%' }}
                          placeholder=""
                          onChange={this.inputChange}
                          />
                      )}
                      </Form.Item>
                  </Col>
                  <Col span={12}>
                      <Form.Item label="* "> 
                      {getFieldDecorator('brithday', {
                          rules: [{ required: true, message: '' }],
                      })(
                          <DatePicker 
                            placeholder=""
                            format="YYYY-MM-DD"
                            style={{width: '100%'}}
                            onChange={this.brithdayChange}
                          />
                          // <DatePicker.RangePicker
                          // style={{ width: '100%' }}
                          // getPopupContainer={trigger => trigger.parentNode}
                          // />
                      )}
                      </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col span={12}>
                    <Form.Item
                      label="* "
                    >
                      {getFieldDecorator('residence', {
                        initialValue: ['', '', ''],
                        rules: [{ type: 'array', required: true, message: '!' }],
                      })(
                        <Cascader
                        options={residences} 
                        onChange={this.residenceChange}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="* "
                    > 
                      {getFieldDecorator('detailedAddr', {
                        rules: [{ required: true, message: '!' }],
                      })(
                        <Input
                        style={{ width: '100%' }}
                        name="detailedAddr"
                        placeholder=""
                        onChange={this.inputChange}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col span={12}>
                      <Form.Item label="* ">
                      {getFieldDecorator('phoneNumber', {
                          rules: [{ required: true, message: '' },
                                  { pattern:new RegExp(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/, "g"),  message: ''}
                        ],
                      })(
                          <Input
                          style={{ width: '100%' }}
                          name="phoneNumber"
                          placeholder=""
                          type='number'
                          addonBefore={prefixSelector}
                          onChange={this.inputChange}
                          />
                      )}
                      </Form.Item>
                  </Col>
                  <Col span={12}>
                      <Form.Item label="* ">
                      {getFieldDecorator('zipcode', {
                          rules: [{ required: true, message: '' },
                                  { pattern:new RegExp(/^[1-9][0-9]{5}$/, "g"),  message: ''}
                        ],
                      })(
                          <Input
                          style={{ width: '100%' }}
                          name="zipcode"
                          placeholder=""
                          type='number'
                          onChange={this.inputChange}
                          />
                      )}
                      </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col span={12}>
                      <Form.Item label="* ">
                      {getFieldDecorator('realName', {
                          rules: [{ required: true, message: '' }],
                      })(
                          <Input
                          style={{ width: '100%' }}
                          name="realName"
                          placeholder=""
                          type='text'
                          onChange={this.inputChange}
                          />
                      )}
                      </Form.Item>
                  </Col>
                  <Col></Col>
                </Row>
                <Row gutter={16}>
                  <Col span={24}>
                      <Form.Item label="">
                      {getFieldDecorator('description')(
                        <Input.TextArea 
                        rows={3} 
                        name='description'
                        placeholder="" 
                        onChange={this.inputChange}
                        />)
                      }
                      </Form.Item>
                  </Col>
                </Row>
            </Form>
            <div
                style={{
                position: 'absolute',
                left: 0,
                bottom: 0,
                width: '100%',
                borderTop: '1px solid #e9e9e9',
                padding: '10px 16px',
                background: '#fff',
                textAlign: 'right',
                }}
            >
                <Button onClick={this.onClose} style={{ marginRight: 8 }}>
                
                </Button>
                <Button onClick={this.onSubmit} type="primary">
                
                </Button>
            </div>
            </Drawer>
            
        </div>
      </BrowserRouter>
    )
  }
}

export default Form.create()(App);
